<template>
      <div class="build">
          <template v-if="loaded">
          <div v-if="canEditor">
            <div class="cell_reason">
                申请信息<span class="font_red" style="float: right">{{statusName}} </span>
            </div>
            <template  v-if="statusName == '已驳回'">
            <div class="cell_reason font_red">
                驳回原因：{{reason}}
            </div>
            <div style="text-align:center;" class="cell_reason">
                <div class="submit_btn" @click="submit('reSub')">
                重新提交申请
                </div>
            </div>
            </template>
            </div>
        <van-cell-group class="input_cell">
            <van-field v-model.trim="form.name" :disabled="form.name"  label="申请人" placeholder="请输入申请人名称"  :required="!canEditor" />
            <van-field v-model.trim="form.phone" :disabled="canEditor" clearable label="联系方式" placeholder="请输入申请人电话，必填"  :error-message="errorPhone"  :required="!canEditor" />
            <van-field v-model.trim="form.company"  :disabled="true"  v-if="columns.length>1 && !canEditor"    type="textarea"  rows="1" autosize label="工作单位" placeholder="请输入申请企业名称，必填"  :required="!canEditor" >
                <div slot="button"  @click="showCompany">
                    <div class="triangle">
                        <div class="t_content"></div>
                    </div>
                </div>
            </van-field>
              <van-field v-model.trim="form.company"  v-if="columns.length<=1 || canEditor" label="工作单位"  :required="!canEditor"  type="textarea" clearable rows="1" autosize  :disabled="columns.length == 1 || canEditor">
              </van-field>
            <van-field v-model.trim="form.address" :disabled="canEditor" clearable label="单位地址" type="textarea"  rows="2" autosize   placeholder="请输入企业地址信息，必填" :required="!canEditor" />
            <van-field v-model.trim="form.number" :disabled="canEditor" clearable type="number" label="企业党员"  placeholder="请输入现有党员数量，必填"  :required="!canEditor" />
            <van-field v-model="time"  v-if="canEditor"    :disabled="canEditor" clearable  label="申请时间"  :required="!canEditor" />       
           </van-cell-group>
        <div style="text-align:center;padding: 20px 0"  v-if="!canEditor">
            <div class="submit_btn" @click="submit">
            提交申请
            </div>
        </div>
        <div class="flow_path">
            <p style="margin-bottom: 0.22rem;color: #686868;">申请流程</p>
            <div class="info_box">企业提示申请</div>
            <div class="icon"><img src="../../../assets/images/party/u241.png" alt="" /></div>
            <div class="info_box">经党委核实,报上级党委批准</div>
            <div class="icon"><img src="../../../assets/images/party/u241.png" alt="" /></div>
            <div class="info_box">企业召开党员大会</div>
            <div class="icon"><img src="../../../assets/images/party/u241.png" alt="" /></div>
            <div class="info_box">召开第一次支部(委员)会</div>
            <div class="icon"><img src="../../../assets/images/party/u241.png" alt="" /></div>
            <div class="info_box">经党委核实,报上级党委批准</div>
            <div class="icon"><img src="../../../assets/images/party/u241.png" alt="" /></div>
            <div class="info_box">党支部正式成立</div>
        </div>
          <bottom /> 
          <company v-on:updateCompany="doUpdateCompany" :show="companyShow" :columns="columns"></company>
          </template>
          <template v-else>
              <div style=" width: 100%;padding-top: 20px; font-size: 14px; color: #ccc; text-align: center;">
                  加载中
              </div>
          </template>
  </div>
</template>
<script>  
  import Bottom from "../common/bottom.vue";
  import Company from "../common/company.vue";
  import {isPhone} from "@/common/valid.js" 
  export default {
    name: 'pBuild',
    data() {
      return {
          form: {
            id: '',  // 用来编辑
            name: '',
            phone: '',
            company: '',
            address: '',
            number: '',
            zone_id: localStorage['h5_zone_id']
          },
          time: '',
          canEditor: false,    // 数据到达审核状态不能修改
          errorPhone: '',
          statusName:'',
          reason: '',  // 驳回原因
          companyShow: false,
          columns: [],
          loaded: false
      }
    },
    components: {Bottom, Company},
    created(){
        this.getPartyData();
    }, 
    watch:  {
        "form.phone": function() {
            if (isPhone(this.form.phone)) { 
                this.errorPhone = '';
            }else {
                this.errorPhone = '请输入正确手机号';
            }
        }
    },
    methods: {
        doUpdateCompany(data) {
            if(data) {
                this.form.company = data;
            }
            this.companyShow = false;

        },
        showCompany() {
            this.companyShow = true;
        },
        getData() {  // 获取用户信息
            if(sessionStorage.users) {
                var data = JSON.parse(sessionStorage.users)
                this.form.name = data.name;
                this.form.phone = data.phone;
                this.form.company = data.company[0] || '';
                this.columns =  JSON.parse(JSON.stringify(data.company));
            }

        },
        getPartyData() {  // 获取用户填写的党支信息
            this.axios.get('/app/party/detail?type=0').then(res => {
                if (res.s == 1) {  
                    this.loaded = true;
                    if(res.d != '') {
                        this.canEditor = true;
                        this.form.id = res.d.id;
                        this.form.name = res.d.name;
                        this.form.phone = res.d.phone;
                        this.form.company = res.d.company;
                        this.form.address = res.d.address;
                        this.form.number= res.d.number;
                        this.time = res.d.create_time;
                        this.statusName = res.d.status == 0 ? '待审核': res.d.status == 1 ? '已通过': '已驳回';
                        this.reason = res.d.operation_remark;
                        if(sessionStorage.users) {
                        var data = JSON.parse(sessionStorage.users)
                        this.columns =  JSON.parse(JSON.stringify(data.company)); 
                        }
                    }else {    // res.d  为空时  获取原始用户数据
                        this.getData();
                    }
                }  
            }) 
        },
        submit(type) {
            if(type == 'reSub') {
                this.canEditor = false;
                return;
            }
            var form = this.form; 
            if(form.name == '' || form.phone == '' || form.company == '' || form.address == '' || form.number == ''){
                this.$toast('请输入完整信息');
                return;
            }
            if(this.errorPhone != '') {
                return;
            }

            this.axios.post('/app/party/partyBuild',form).then(res => {
                if (res.s == 1) {  
                   this.$toast('提交成功!'); 
                   this.getPartyData();
                   return;
                } 
            }) 

        }
    }
  }
  </script>

<style lang="scss" scoped>
@function rem($px) {
    @return $px / 50px * 1rem;
}
  .build {
      padding-top: rem(12px);


    .input_cell {
        .van-cell:not(:last-child)::after {
            left: 0;
        }
        .van-cell {
            padding: 18px 15px;
            font-size: rem(16px)
        }
    }
    .submit_btn {
        display: inline-block;
        font-size: rem(18px);
        margin: 0 auto; 
        border: none;
        width: 80%;
        height: rem(48px);
        line-height: rem(48px);
        outline-style: none;
        text-align: center;
        border-style: none;
        border-radius: 5px;
        background-color: #F24B3B;
        color: #FFFFFF;

    }
    .flow_path{
        padding-top: 0.625rem;
        border-top: 10px solid #eeeeee;
        background: #FFF;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-bottom: rem(1px) solid #eee;
    }
    .info_box{
        padding: 0 rem(40px);
        height: rem(40px);
        color:#F24B3B ;
        line-height:  rem(40px);
        text-align: center;
        background: #FFF1F0;
        border-radius: 15px;
        margin-bottom: 2px;
    }
    
    .info_box:last-child{
        margin-bottom: rem(36px);
    }
    .icon{
        margin: rem(6px) 0;
    }
    .icon>img{
        max-height: 100%;
        max-width: 100%;
        width: rem(16px);
        height: auto;
    }
    .cell_reason {
        padding: 18px 15px;
        font-size: 0.32rem; 
        background: #fff;
        position: relative;
        &:nth-child(1) {
            border-top: rem(1px) solid #eee;
        }
        &:nth-child(3) {
            &::after {
                content: ' ';
                border-bottom: 0;
            }
        }
        &::after {
            content: ' ';
            position: absolute;
            pointer-events: none;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            right: 0;
            left: 0;
            bottom: 0;
            -webkit-transform: scaleY(.5);
            transform: scaleY(.5);
            border-bottom: 1px solid #eee;
        }
    }
    button, input, select, textarea {
        line-height: 24px;

    }
  }
</style>

